.testimonials {
  margin-top: 150px;

  .reviews {
    text-align: center;

    .carousel-indicators {
      li {
        background-color: #7beec7;
      }
    }

    .photo {
      height: 80px;
      width: 80px;
      border-radius: 50%;
      overflow: hidden;
      margin: 0 auto;
    }

    .review-text {
      line-height: 30px;
      color: #999;
      margin: 30px 0;
    }

    .author_name {
      background-color: #7beec7;
      padding: 0;
      width: 150px;
      height: 50px;
      border: none;
      color: #fff;
      margin: 0 auto;
      margin-bottom: 60px;
    }
  }

  .clients {
    .client-wrapper {
      margin-bottom: 30px;

      .client {
        // border: 1px solid #ececec;
        text-align: center;
        padding: 20px;
        max-height: 100px;

        a {
          img {
            max-width: 100%;
            max-height: 100px;
          }
        }
      }
    }

    .client-wrapper:nth-last-child(-n + 3) {
      margin-bottom: 0;
    }
  }
}
