.categories {
  ul {
    list-style: none;
    text-transform: uppercase;
    padding: 0;

    li {
      margin: 20px 0;

      a {
        transition: all 0.2s ease-in;

        text-decoration: none;
        height: 100%;
        padding: 0 10px;
        display: inline-block;
        font-family: "Montserrat", sans-serif;
        font-weight: 700;
        margin-left: 20px;
      }
    }

    a.not-active {
      color: yellow;
    }

    a.selected {
      color: white;
      margin-left: 0;
      background-color: #7beec7;
    }
  }
}
