.page_pagination {
  width: 100%;
  height: 50px;
  margin: 50px 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  a {
    height: 100%;
    width: 50px;
    transition: all 0.2s ease-in;
    justify-content: center;
    display: flex;
    align-items: center;
    font-size: 24px;
  }

  a:hover {
    background-color: #7beec7;
    color: white;
  }

  .arrow {
    display: flex;
    flex-direction: row;
  }
}
