.contacts {
  margin-top: 150px;
  form {
    .input-wrapper {
      width: 100%;
      height: 50px;
      margin-bottom: 30px;
    }

    .textarea-wrapper {
      width: 100%;
      margin-bottom: 30px;
    }

    input,
    textarea {
      background-color: #fefcf9;
      width: 100%;
      height: 100%;
    }

    button {
      background-color: #7beec7;
      padding: 0;
      width: 150px;
      height: 50px;
      border: none;
      color: #fff;
    }
  }

  .contact-info {
    margin-top: 100px;
    .contact-title {
      span {
        font-family: Montserrat;
        font-size: 18px;
        font-weight: 700;
        line-height: 48px;
        text-transform: uppercase;
      }

      p {
        color: #999;
        line-height: 30px;
      }
    }

    .address {
      color: #999;
      line-height: 30px;

      .email-text {
        font-weight: 700;
      }
    }

    .social {
      ul {
        display: flex;
        flex-direction: row;
        list-style: none;
        padding: 0;

        li {
          margin-right: 15px;
        }
      }
    }
  }

  .maps {
    width: 100%;
    height: 840px;
  }
}
