.post {
  margin-bottom: 100px;

  .preview {
    max-height: 750px;
  }

  ul.post-info {
    padding-left: 0;
    margin-top: 30px;
    display: flex;
    flex-direction: row;
    align-items: center;

    li {
      margin: 0 25px;
    }
    li:first-child {
      margin: 0 25px 0 0;
    }
  }

  span {
    font-size: 18px;
  }

  p {
    color: #999999;
    font-weight: 400;
    line-height: 24px;
    margin-bottom: 25px;
  }

  a {
    font-family: Montserrat;
    font-weight: 700;
    line-height: 24px;
    text-transform: uppercase;
    text-decoration: none;
  }
}
