.main-header {
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 9;

  .menu-mobile,
  .menu-desktop {
    ul.second-level {
      list-style: none;
      padding: 0;
      background-color: #60606e;
      margin-left: 7px;
      position: absolute;
      top: 50px;
      left: 0;
      min-width: 200px;
      transition: all 0.2s ease-in;
      opacity: 0;
      display: none;

      li {
        transition: all 0.2s ease-in;
        a {
          display: inline-block;
          width: 100%;
          height: 100%;
          padding: 0 20px;
          color: white;
          text-transform: uppercase;
          text-decoration: none;
          font-size: 12px;
        }
      }

      li:hover {
        background-color: #404051;
      }
    }

    ul {
      li {
        position: relative;
        a {
          text-transform: uppercase;
          font-size: 12px;
        }
      }
    }
  }

  .menu-desktop {
    ul {
      li {
        a:after {
          content: "";
          position: absolute;
          top: 30px;
          left: 5px;
          width: 50%;
          height: 1px;
          background-color: #60606e;
          opacity: 0;
          transition: all 0.2s ease-in-out;
        }
      }

      li:hover {
        .second-level {
          opacity: 1;
          display: inline-block;
        }
      }

      li:hover a:after {
        top: 15px;
        opacity: 1;
      }
    }
  }

  .menu-mobile {
    .mobile-nav {
      user-select: none;
      color: #fff;
      text-align: center;
      min-width: 300px;
      position: absolute;
      right: 0;
      top: 100px;
      background-color: #60606e;

      ul {
        margin-bottom: 10px;
        li {
          a {
            padding: 0;
            width: 100%;
            height: 40px;
            line-height: 40px;
            text-align: center;
            color: white;
            font-family: "Montserrat", sans-serif;
            font-weight: 700;
            font-size: 12px;
            text-transform: uppercase;
          }

          a:hover {
            background: #404051;
          }
        }
      }
    }

    .menu-icon {
      cursor: pointer;

      .line {
        height: 1px;
        width: 30px;
        background-color: #60606e;
        -webkit-transition: all 0.2s ease-in-out;
        transition: all 0.2s ease-in-out;
      }

      .line:nth-child(2) {
        margin: 7px 0;
        width: 15px;
      }

      .line:last-child {
        width: 20px;
      }
    }

    .menu-icon:hover {
      .line {
        width: 30px;
      }
    }

    .menu-icon:active {
      .line {
        width: 30px;
      }
    }

    ul {
      li.selected {
        .second-level.down {
          margin: 0;
          width: 100%;
          background: #50505f;
          position: static;
          display: inline-block;
          transition: 0.3s;
          opacity: 1;
        }
      }
    }
  }
}

/* Large desktops and laptops */
@media screen and (min-width: 1200px) {
  .menu-mobile {
    display: none;
  }
}

/* Portrait tablets and medium desktops */
@media (min-width: 992px) and (max-width: 1199px) {
  .menu-mobile {
    display: none;
  }
}

/* Portrait tablets and small desktops */
@media screen and (max-width: 991px) {
  .menu-desktop {
    display: none;
  }

  .menu-mobile {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

