.card {
  margin-bottom: 30px;
  border: none;
  transition: all 0.2s ease-in;
  border-top: 5px solid #7beec7;
  position: relative;
  padding: 30px;

  .card-body {
    padding: 0;

    .price {
      padding: 5px 20px;
      background-color: #7beec7;
      width: 165px;
      border: none;
      color: #fff;
      line-height: 20px;
    }

    .list {
      list-style-image: url(../../../../../asssets/img/img-list.png);
      margin: 50px 0;
      padding-left: 20px;

      li {
        line-height: 36px;
      }
    }

    button {
      background-color: #7beec7;
      padding: 0;
      width: 150px;
      height: 50px;
      border: none;
      color: #fff;
    }
  }
}

.card::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 0 solid transparent;
  border-top: 10px solid #7beec7;
}
.card:hover {
  transform: scale(1.01);
  box-shadow: 0 0 50px rgba(0, 0, 0, 0.25);
}
