.agency {
  height: auto;
  width: 100%;
  margin-top: -300px;

  .row {
    .col-md-6 {
      margin-left: 75px;
    }
  }

  .section-title {
    margin: 0 auto 30px;
    width: 100%;
    text-align: left;
  }

  p {
    font-size: 16px;
    line-height: 30px;
  }

  button {
    background-color: #7beec7;
    padding: 0;
    width: 150px;
    height: 50px;
    border: none;
    color: #fff;
  }
}

