/* Portrait tablets and small desktops */
@media screen and (max-width: 991px) {
  .agency {
    margin-top: 30px;

    .row {
      .col-lg-6.col-md-12 {
        text-align: center;

        .section-title {
          margin-top: 20px;
        }
      }
    }
  }
}

