.news-letter {
  padding: 50px 0;
  margin-top: 150px;
  background-color: #7beec7;

  .container {
    .row {
      .work-together {
        span {
          display: block;
          color: #fff;
          font-size: 30px;
          font-weight: bold;
          line-height: 1.6;
        }

        button {
          background-color: #ffffff;
          padding: 0;
          margin: 30px 0 20px;
          width: 150px;
          height: 50px;
          border: none;
          color: #7beec7;
        }
      }

      .form-block {
        background-color: #fff;

        .white-section {
          padding: 20px 5px;
          span {
            font-size: 24px;
            line-height: 1.5;
          }

          p {
            color: #999999;
            line-height: 1.5;
            font-size: 16px;
            margin-bottom: 30px;
          }

          form {
            div {
              height: 50px;
              width: 100%;
              border: 1px solid #e5e5e5;

              input {
                width: 100%;
                height: 100%;
                border: 0;
                border-radius: 0;
                color: #999;
                padding: 0 20px;
              }
            }

            button {
              margin-top: 20px;
              background-color: #7beec7;
              padding: 0;
              width: 150px;
              height: 50px;
              border: none;
              color: #fff;
            }
          }
        }
      }
    }
  }
}

