.popular-posts {
  .post-wrapper {
    display: flex;
    width: 100%;
    margin-bottom: 15px;

    .post-image {
      height: 80px;
      width: 80px;
      transition: all 0.2s ease-in;
    }

    .post-image:hover {
      opacity: 0.6;
    }

    .post-title {
      margin-left: 20px;

      span.span-title {
        display: inline-block;
        font-weight: 700;
        line-height: 18px;
        text-transform: uppercase;

        a {
          text-decoration: none;
        }
      }

      span.span-date {
        display: inline-block;
        color: #9a9a9a;
        font-size: 12px;
        font-weight: 400;
        line-height: 18px;
      }
    }
  }
}
