.light-gray-section {
  margin-bottom: 20px;
  background-color: #efeff0;
  padding: 15px 30px;

  .row {
    display: flex;
    align-items: center;
    justify-content: space-between;

    p {
      line-height: 50px;
      font-style: italic;
      color: #999;
      margin: 0;
    }
  }
  button {
    background-color: #7beec7;
    padding: 0;
    width: 150px;
    height: 50px;
    border: none;
    color: #fff;
  }
}
