.widget.widget-tags {
  .tags {
    display: flex;
    flex-flow: row wrap;

    .tag {
      a {
        display: inline-block;
        background-color: #e5e5e5;
        color: #999999;
        font-family: Montserrat;
        font-size: 12px;
        font-weight: 700;
        text-transform: uppercase;
        margin-right: 5px;
        margin-bottom: 5px;
        padding: 5px 10px;
        text-decoration: none;
      }
    }

    .tag:hover {
      a {
        background-color: #f2f2f2;
        color: #60606e;
      }
    }
  }
}
