.main-footer {
  .container {
    .row {
      padding: 50px 0;

      .menu {
        padding-left: 0;

        nav {
          ul {
            li {
              margin: 0 20px;

              a {
                padding-left: 0;
                text-transform: uppercase;
                color: #9a9a9a;
              }
            }

            li:first-child {
              margin-right: 20px;
            }
          }
        }
      }

      .license {
        display: flex;
        justify-content: end;
        align-items: center;
        flex-direction: column;

        div {
          padding-top: 14px;
          color: #9a9a9a;
          text-transform: uppercase;
        }

        ul {
          display: flex;
          margin: 30px 0 0 auto;
          padding-left: 0;

          li {
            list-style: none;
            margin-right: 20px;

            a {
              font-size: 18px;
            }
          }

          li:last-child {
            margin-right: 0;
          }
        }
      }
    }
  }
}

/* Large desktops and laptops */
@media screen and (min-width: 1200px) {
}

/* Portrait tablets and medium desktops */
@media screen and (min-width: 992px) and (max-width: 1199px) {
  .main-footer {
    .menu {
      nav {
        ul {
          li {
            margin-right: 15px;
          }
        }
      }
    }

    .license {
      div {
        text-align: end;
      }
    }
  }
}

/* Portrait tablets and small desktops */
@media screen and (max-width: 991px) {
  .main-footer * {
    text-align: center;
  }

  .main-footer {
    .menu {
      nav {
        display: block;

        ul {
          li {
            margin: 0 20px;
          }
        }
      }
    }
  }

  .license {
    ul {
      width: 100%;
      justify-content: space-around;
    }
  }
}

/* Landscape phones and portrait tablets */
@media screen and (max-width: 767px) {
}

/* Landscape phones and smaller */
@media screen and (max-width: 480px) {
