.category {
  padding: 0;
  li {
    list-style: none;

    a {
      padding: 20px;
      border-bottom: 1px solid #bfbfbf;
      transition: all 0.2s ease-in;

      text-decoration: none;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
    }

    a:hover {
      border-bottom: 1px solid #7beec7;
      color: inherit;
    }
  }
}
