section.single-post-section {
  .post {
    .post-media {
      max-height: 500px;
    }

    p {
      line-height: 30px;
    }

    ul.post-list {
      padding-left: 20px;
      list-style-image: url(../../asssets/img/img-list.png);

      li {
        line-height: 30px;
      }
    }

    .gray-block {
      width: 100%;
      padding: 30px;
      border-left: 10px solid #7beec7;
      font-style: italic;
      line-height: 30px;
      color: #999;
      background-color: #efeff0;
      margin-bottom: 30px;
    }
  }
}
